import styled from '@emotion/styled';
import { StaticImage } from 'gatsby-plugin-image';

export default function BackgroundImage() {
  return (
    <HeaderBackground>
      <StaticImage
        src="../../content/img/common/back1.jpg"
        alt="header background"
        placeholder="blurred"
        layout="fullWidth"
      />
    </HeaderBackground>
  );
}

const HeaderBackground = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 68rem;

  img {
    width: 100%;
    height: 68rem;
  }

  div[data-gatsby-image-wrapper] {
    height: 68rem;
  }

  @media (max-width: 780px) {
    height: 40rem;

    img {
      width: 100%;
      height: 40rem;
    }

    div[data-gatsby-image-wrapper] {
      height: 40rem;
    }
  }
`;
